.faq {
  margin-bottom: 80px;

  @include m-b-tablet-down {
    margin-bottom: 40px;
  }

  &:not(&--main) {
    .faq__container {
      position: relative;
      padding-bottom: 50px;

      &::after {
        @extend .section-line;
        top: 100%;
        right: 0;
      }
    }
  }

  &__title {
    margin-bottom: 30px;
  }

  &__content {
    margin-bottom: 35px;
  }

  &__btn {
    margin: 0 auto;
    margin-bottom: 70px;
  }

  &__more {
    @extend .button-default;
    font-weight: 400;
    font-size: 14px;
    padding: 10px 0;
    color: var(--gray);
    text-decoration: underline;
  }

  &__footer {

    &-text {
      p {
        font-size: 18px;
        line-height: 28px;
        margin-bottom: 25px;

        &:not(:first-of-type) {
          height: 0;
          opacity: 0;
          visibility: hidden;
          margin-bottom: 0;
          transition: opacity var(--slow-transition), margin var(--slow-transition) ;
        }
      }

      &.active {
        p {
          height: 100%;
          opacity: 1;
          visibility: visible;
          margin-bottom: 25px;
        }
      }
    }

    &-grid {
      display: grid;
      grid-template-columns: 360px auto;
      grid-column-gap: 25px;

      @include m-b-down(800px) {
        grid-template-columns: 1fr;
      }

      &-side {
        height: fit-content;

        @include m-b-down(800px) {
          margin-bottom: 20px;
        }

        .video-preview {
          width: 100%;

          @include m-b-down(800px) {
            display: flex;
          }

          &__content {
            display: flex;
            width: 100%;
            height: 200px;

            video {
              max-width: 360px;
              height: auto;
              object-fit: cover;
              max-height: 200px;
              margin: 0 auto;
            }
          }
        }
      }
    }
  }
}
